import Apply from "../pages/Apply";

const teacherApply = [
    {
        path: 'apply',
        children: [
            {
                path: 'departmentChange',
                component: () => import('../pages/Apply/DepartmentChange'),
                meta: {
                    title: '',
                    Auth: 'teacher'
                }
            },
            {
                path: 'travelReimbursement',
                component: () => import('../pages/Apply/TravelReimbursement'),
                meta: {
                    title: '',
                    Auth: 'teacher'
                }
            }
        ]
    }
]

export default teacherApply;