import {Menu, MenuProps} from 'antd';
import {EditOutlined, FormOutlined, BarChartOutlined, HomeOutlined, CommentOutlined} from '@ant-design/icons';
import React from 'react';
import './index.scss'
import {Link} from 'react-router-dom';
import {useSelector} from "react-redux";

type MenuItem = Required<MenuProps>['items'][number];

function getItem(
    label: React.ReactNode,
    key: React.Key,
    icon?: React.ReactNode,
    children?: MenuItem[],
    type?: 'group',
): MenuItem {
    return {
        key,
        icon,
        children,
        label,
        type,
    } as MenuItem;
}

const teacher: MenuProps['items'] = [
    getItem((<Link to={'/home/teacher/index'}></Link>), 'teacherHome', <HomeOutlined/>),
    getItem('', 'apply', <EditOutlined/>, [
        getItem((
            <Link to={'/home/teacher/apply/departmentChange'}></Link>), 'teacherApplyDepartmentChange'),
        getItem((<Link
            to={'/home/teacher/apply/travelReimbursement'}></Link>), 'teacherApplyTravelReimbursement'),
        getItem((<Link to={'/home/teacher/apply/leave'}></Link>), 'teacherApplyLeave'),
        getItem((<Link to={'/home/teacher/apply/procurement'}></Link>), 'teacherApplyProcurement'),
        getItem((<Link to={'/home/teacher/apply/report'}></Link>), 'teacherApplyReport'),
    ]),
    getItem('', 'record', <BarChartOutlined/>, [
        getItem((<Link to={'/home/teacher/record/departmentChange'}></Link>), 'teacherRecordLeaveList'),
        getItem((<Link
            to={'/home/teacher/record/travelReimbursement'}></Link>), 'teacherTravelReimbursement'),
        getItem((<Link to={'/home/teacher/record/leave'}></Link>), 'teacherRecordLeave'),
        getItem((<Link to={'/home/teacher/record/procurement'}></Link>), 'leaveProcurement'),
        getItem((<Link to={'/home/teacher/record/report'}></Link>), 'teacherRecordReport'),
    ]),
];
const department: MenuProps['items'] = [
    getItem((<Link to={'/home/department'}></Link>), 'department', <HomeOutlined/>),
    getItem('', 'apply', <FormOutlined/>, [
        getItem((<Link to={'/home/department/Apply/notice'}></Link>), 'notice'),
        getItem((<Link to={'/home/department/Apply/departmentChange'}></Link>), 'departmentChange'),
    ]),
    getItem('', 'sub4', <BarChartOutlined/>, [
        getItem('', '9'),
        getItem('', '10')
    ]),
];
const leader: MenuProps['items'] = [
    getItem((<Link to={'/home/department'}></Link>), 'home', <HomeOutlined/>),
    getItem('', 'apply', <FormOutlined/>, [
        getItem((<Link to={'/home/department/Apply/notice'}></Link>), 'notice'),
        getItem((<Link to={'/home/department/Apply/departmentChange'}></Link>), 'departmentChange'),
    ]),
    getItem('', 'notion', <CommentOutlined/>, [
        getItem((<Link to={'/notion/add'}></Link>), 'add'),
        getItem((<Link to={'/notion/show'}></Link>), 'show'),
    ]),
    getItem('', 'sub4', <BarChartOutlined/>, [
        getItem('', '9'),
        getItem('', '10')
    ]),
];
const defaultMenu: MenuProps['items'] = [];

const RenderMenu: React.FC = () => {
    const userType = useSelector((state: {
        userType: {
            value: string
        }
    }) => state.userType.value)
    // const [theme, setTheme] = useState('light');
    const RenderMenu = (): MenuProps['items'] => {
        switch (userType) {
            case 'teacher':
                return teacher
            case 'department':
                return department
            case 'leader':
                return leader
            default:
                return defaultMenu
        }
    }
    const menu: MenuProps['items'] = RenderMenu()
    return (
        <Menu
            theme={"light"}
            mode="inline"
            items={menu}
            style={{minWidth: 0, flex: 'auto'}}
        />
    )
}

export default RenderMenu;