import {Breadcrumb} from "antd";
import React from "react";
import {Link, useLocation} from "react-router-dom";

const RenderBreadcrumb = () => {
    const location = useLocation();
    const breadcrumbNameMap: Record<string, string> = {
        '/home': 'OA ',
        '/home/teacher': '',
        '/home/departments': '',
        '/home/leader': '',
        '/home/teacher/index': '',
        '/home/department/index': '',
        '/home/leader/index': '',
        '/home/teacher/apply': '',
        '/home/teacher/apply/departmentChange': '',
        '/home/teacher/apply/travelReimbursement': '',
        '/home/teacher/apply/leave': '',
        '/home/teacher/apply/procurement': '',
        '/home/teacher/apply/report': '',
        '/home/teacher/record/departmentChange': '',
        '/home/teacher/record/travelReimbursement': '',
        '/home/teacher/record/leave': '',
        '/home/teacher/record/procurement': '',
        '/home/teacher/record/report': '',
    };
    const pathSnippets = location.pathname.split('/').filter((i) => i);
    const extraBreadcrumbItems = pathSnippets.map((_, index) => {
        const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
        // '/home' 
        if (index < 4) {
            return (
                <Breadcrumb.Item key={url}>
                    {breadcrumbNameMap[url]}
                </Breadcrumb.Item>
            )
        }
        return (
            <Breadcrumb.Item key={url}>
                <Link to={url}>{breadcrumbNameMap[url]}</Link>
            </Breadcrumb.Item>
        );
    });
    const breadcrumbItems = [
        <Breadcrumb.Item key="home">
            {/*<Link to="/home"></Link>*/}
        </Breadcrumb.Item>,
    ].concat(extraBreadcrumbItems);
    return (
        <Breadcrumb>{breadcrumbItems}</Breadcrumb>
    )
}


export default RenderBreadcrumb;